.week-offer {
  margin-top: 29px;
}

@media (min-width: $tablet-width) {
  .week-offer {
    position: relative;
    flex-wrap: wrap;
    display: flex;
    align-items: center;
    overflow: hidden;
    margin-top: 44px;
  }
}

@media (min-width: $desktop-width) {
  .week-offer {
    padding-top: 54px;
    padding-left: 97px;
    margin-top: 70px;
  }
}

.week-offer__wrapper {
  margin-top: 17px;
}

@media (min-width: $tablet-width) {
  .week-offer__wrapper {
    width: 292px;
    margin-top: 49px;
  }
}

@media (min-width: $desktop-width) {
  .week-offer__wrapper {
    margin-top: 48px;
    margin-left: 128px;
  }
}

.week-offer__title {
  text-transform: uppercase;
  color: #63d1bb;
  line-height: 30px;
}

@media (min-width: $tablet-width) {
  .week-offer__title {
    width: 70px;
    margin-left: auto;
    margin-right: 29px;
    margin-top: 7px;
    text-align: center;
    line-height: 17px;
  }
}

@media (min-width: $desktop-width) {
  .week-offer__title {
    margin-left: 0;
    order: -1;
  }
}

@media (min-width: $tablet-width) {
  .week-offer__name {
    margin-top: 0;
    margin-left: -2px;
  }
}

@media (min-width: $desktop-width) {
  .week-offer__name {
    width: 722px;
    margin-left: 24px;
    z-index: 3;
  }
}

.week-offer__description {
  line-height: 24px;
  margin-bottom: 18px;
}

@media (min-width: $tablet-width) {
  .week-offer__description {
    margin: 0;
    font-size: 17px;
    line-height: 30px;
  }
}

.week-offer__price {
  @include font-bold;
  margin-top: 13px;
  margin-bottom: 22px;
  margin-left: 0;
  margin-right: 0;
  font-size: 17px;
  text-align: center;
}

@media (min-width: $tablet-width) {
  .week-offer__price {
    margin-top: 53px;
    font-size: 20px;
  }
}

@media (min-width: $desktop-width) {
  .week-offer__price {
    margin-top: 106px;
  }
}

@media (min-width: $tablet-width) {
  .week-offer__image {
    position: absolute;
    top: 108px;
    right: 50px;
  }
}

@media (min-width: $desktop-width) {
  .week-offer__image {
    position: absolute;
    top: 0;
    right: 50px;
    padding-left: 16px;
  }
}

.week-offer__field {
  width: 94px;
}

@media (min-width: $tablet-width) {
  .week-offer__value {
    padding-left: 0;
  }
}

@media (min-width: $desktop-width) {
  .week-offer__value {
    padding-left: 0;
  }
}

.week-offer__table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 27px;

  & td {
    padding-top: 4px;
    padding-bottom: 5px;
    border-bottom: 1px solid #ececec;
  }
}

@media (min-width: $tablet-width) {
  .week-offer__table {
    width: 100%;
    margin-top: 35px;
  }
}

@media (min-width: $desktop-width) {
  .week-offer__table {
    margin-top: 49px;
  }
}

@media (min-width: $tablet-width) {
  .week-offer__button {
    width: 292px;
    margin-top: 31px;
  }
}
